<template>
  <section id="landing">
    <div class="wave-content">
      <h2>{{ title }}</h2>
      <img class="logo" src="../assets/sp-logo.png" alt="selfpick" />
    </div>
    <div class="wave wave-1" />
    <div class="wave wave-2" />
    <div class="wave wave-3" />
    <div class="wave wave-4" />
  </section>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Landing',
  data() {
    return {
      msg: 'section 1 Ya',
      title: 'We pick our own path',
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.wave-content h2', {
      origin: 'left',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  @keyframes waving {
    0% {
        transform: translateX(0) translateZ(0) scaleY(1)
    }
    50% {
        transform: translateX(-25%) translateZ(0) scaleY(0.9)
    }
    100% {
        transform: translateX(-50%) translateZ(0) scaleY(1)
    }
  }

  section {
    position: relative;
    color: #fff;
    overflow: hidden;
  }

  .wave-content {
    height: 100vh;
    max-height: 750px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 5;

    h2 {
      position: relative;
      font-size: 20px;
      font-weight: 300;
      letter-spacing: 3px;
      text-transform: uppercase;
      z-index: 2;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      padding: 5%;
      z-index: 1;
    }
  }

  .wave {
    width: 2880px;
    height: 100%;
    position: absolute;
    top: -5%;
    left: 0;

    @media screen and (min-width: 768px){
      width: 3840px;
    }
  }

  .wave-1 {
    background: url('../assets/01.png') repeat-x;
    background-size: 1440px 100%;
    z-index: 4;
    animation: waving 15s 100ms linear infinite;

    @media screen and (min-width: 768px){
      background-size: 1920px 100%;
      animation: waving 15s 100ms linear infinite;
    }
  }

  .wave-2 {
    background: url('../assets/02.png') repeat-x;
    background-size: 1440px 100%;
    z-index: 3;
    animation: waving 18s 200ms linear infinite;

    @media screen and (min-width: 768px){
      background-size: 1920px 100%;
      animation: waving 18s 200ms linear infinite;
    }
  }

  .wave-3 {
    background: url('../assets/01.png') repeat-x;
    background-size: 1440px 100%;
    z-index: 2;
    animation: waving 21s 300ms linear infinite;
    // opacity: 0.8;

    @media screen and (min-width: 768px){
      background-size: 1920px 100%;
      animation: waving 21s 500ms linear infinite;
    }
  }

  .wave-4 {
    background: url('../assets/02.png') repeat-x;
    background-size: 1440px 100%;
    z-index: 1;
    opacity: 0.5;

    @media screen and (min-width: 768px){
      background-size: 1920px 100%;
    }
  }
</style>



